import React from "react"
import "./services.sass"
import scrollTo from 'gatsby-plugin-smoothscroll';

class Services extends React.Component{

    state = {
        serviceSelected: null
    }

    services = [
        {
            title: "Consulta de ginecología",
            description: "Promoción y prevención de patologías ginecológicas."+
             "Detección de enfermedades en la mujer, diagnóstico y tratamiento"
        },
        {
            title: "Consulta de ginecología en planificación familiar",
            description: "Se explican los diferentes métodos de planificación asesorando"+
                "a cada paciente a elegir el mejor método para planificar. "
        },
        {
            title: " Consulta de prevención de cáncer de cuello uterino ",
            description: " Toma de citología cervicovaginal. Prueba de detección del Virus del"+
                "Papiloma Humano (VPH). Tratamiento para verrugas genitales. "
        }

    ]

    selectService(index){
        console.log(this.services[index])
        this.setState({serviceSelected:this.services[index]})
        scrollTo('#service-description')
    }

    render (){
        return (
        <div className="services">
        <div className="services__list">
            <div className="services__card">
                <div className="services__card-overlay" tabIndex={0} role = "button" onKeyDown={()=>this.selectService(0)} onClick={()=>this.selectService(0)}></div>
                <i className="services__card-icon">person</i>
                <h3 className="services__card-title">Consulta de ginecología</h3>
            </div>
            <div className="services__card">
                <div className="services__card-overlay" tabIndex={0} role = "button"  onKeyDown={()=>this.selectService(1)} onClick={()=>this.selectService(1)}></div>
                <i className="services__card-icon">group</i>
                <h3 className="services__card-title">Consulta de asesoría en planificación familiar</h3>
            </div>
            <div className="services__card">
                <div className="services__card-overlay" tabIndex={0} role = "button" onKeyDown={()=>this.selectService(2)} onClick={()=>this.selectService(2)}></div>
                <i className="services__card-icon">local_hospital</i>
                <h3 className="services__card-title">Consulta de prevención de cáncer de cuello uterino</h3>
            </div>
        </div>
        <div className="services__prices">
            <div className="services__prices-wrap">
                <div className="services__prices-disclaimer-wrap">
                    <p className="services__prices-disclaimer services__prices-disclaimer-first">
                        *Toda paciente UNIVERSITARIA, presentando su carnet vigente o
                        certificando que se encuentra cursando estudios superiores
                        recibirá una tarifa especial en su consulta particular de ginecología</p>
                    <p className="services__prices-disclaimer services__prices-disclaimer-second">**MEDICINA PRIVADA AL ALCANCE DE TUS MANOS: solicita tu cita
                        por este medio y recibirás un descuento del 20% en las tarifas
                        de consulta particular primera vez y control.</p>
                </div>
                <div className="services__prices-list">
                    <div className="services__price-content" >
                        <div className="services__price-title-wrap">
                            <p className="services__price-title">Consulta primera vez</p>
                        </div>
                    </div>
                    <div className="services__price-content" >
                        <div className="services__price-title-wrap">
                            <p>Consulta control</p>
                        </div>
                    </div>
                    <div className="services__price-content" >
                        <div className="services__price-title-wrap">
                            <p>Teleconsulta</p>
                        </div>
                    </div>
                    <div className="services__price-content" >
                        <div className="services__price-title-wrap">
                            <p>Citología</p>
                        </div>
                    </div>
                </div>
                </div>
            </div>
            <div id="service-description">
                <div className={this.state.serviceSelected?"services__description":"services__display-none"}>
                    <div className="services__description-wrapper">
                        <div className="services__description-title">
                            {this.state.serviceSelected?this.state.serviceSelected.title:''}
                        </div>

                        <div className="services__description-content">
                            {this.state.serviceSelected?this.state.serviceSelected.description:''}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }

}
export default Services
